.contact-details-page .details-container {
  margin-top: 1rem; }
  .contact-details-page .details-container h1.name {
    font-size: 2rem;
    margin: 0;
    color: #e44f65; }
  .contact-details-page .details-container h1 {
    color: #97BAD4; }
  .contact-details-page .details-container h2 {
    font-size: 1rem;
    color: white;
    opacity: 0.6; }

.contact-details-page .edit-icon {
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
  font-size: 1.5em;
  position: absolute;
  right: 1%;
  top: 8%;
  color: white;
  opacity: 0.6;
  cursor: pointer; }
  .contact-details-page .edit-icon:hover {
    color: #947546; }

.contact-details-page .back-icon {
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
  font-size: 1.5em;
  position: absolute;
  left: 1%;
  top: 8%;
  color: white;
  opacity: 0.6;
  cursor: pointer; }
  .contact-details-page .back-icon:hover {
    color: #947546; }

@media (max-width: 500px) {
  .contact-details-page .edit-icon {
    right: 4%; }
  .contact-details-page .back-icon {
    left: 4%; } }



